.password-vault-page .benefits .row4{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin:5% 0;
    padding:0 10% 0 0;
    overflow: hidden;
    .col1{
        width: 52.5%;
        position: relative;
    }
    .col2{
        width: 35%;
    }
    .hexagon{
        content: url('../../../../assets/PasswordVault/hexagon.png');
        width: 100%;
        margin-left: -25%;
    }
    .iphone{
        content: url('../../../../assets/PasswordVault/iPhone.png');
        position: absolute;
        right: 5%;
        top:7.5%;
        width:40%;
        z-index: 2;
    }
    h1{
        font-size: 35px;
        margin-bottom: 25px;
    }
    .item4{
        display: flex;
        align-items: center;
        margin:25px 0;
        &:hover{
            .icon{
                border:1px solid #427cff;
                background: #427cff;
                color:#fff;
            }
            h6{
                color:#427cff;
            }
        }
    }
    .icon{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 75px;
        height: 75px;
        font-size: 35px;
        margin-right: 15px;
        border:1px solid #ccc;
        border-radius: 50%;
        color:#427cff;
        transition: 0.15s ease-in-out;
    }
    .block{
        flex: 1;
    }
    h6{
        font-size: 18px;
        margin-bottom: 7.5px;
    }
    p{
        font-size: 18px;
    }
}

@media(max-width:1401px){
    .password-vault-page .benefits .row4{
        margin:5% 0;
        padding:0 10% 0 0;
        .col2{
            width: 35%;
        }
        h1{
            font-size: 30px;
            margin-bottom: 20px;
        }
        .item4{
            margin:20px 0;
        }
        .icon{
            width: 65px;
            height: 65px;
            font-size: 30px;
            margin-right: 15px;
        }
        h6{
            font-size: 15px;
            margin-bottom: 7.5px;
        }
        p{
            font-size: 15px;
        }
    }
}

@media(max-width:991px){
    .password-vault-page .benefits .row4{
       flex-wrap: wrap;
        margin:30px 0;
        padding:0 7.5%;
        .col1{
            width: 500px;
            max-width: 100%;
            margin:20px auto;
        }
        .col2{
            width: 500px;
            max-width: 100%;
            margin:20px auto;
        }
        .hexagon{
            margin-left: 0;
        }   
    }
}

@media(max-width:991px){
    .password-vault-page .benefits .row4{
        h1{
            font-size: 20px;
        }
        .icon{
            width: 55px;
            height: 55px;
            font-size: 25px;
            margin-right: 15px;
        }
        h6{
            margin-bottom: 5px;
        }
    }
}

//========================================
.password-vault-page .benefits .left-right{
    padding:0 10%;
    .col1{
        width: 40%;
    }
    .other-elements{
        width: 42.5%;
    }
    .created-img{
        position: relative;
        width: 100%;
    }
    .bg-img{
        width: 100%;
        content: url('../../../../assets/PasswordVault/bg-img2.png');
    }
    .top-img{
        content: url('../../../../assets/PasswordVault/top-img2.png');
        width: 75%;
        border-radius: 50%;
        position: absolute;
        top:50%;
        left:50%;
        transform: translate(-50%, -50%);
        transition: 0.35s ease-in-out;
        &:hover{
            border-radius: 10%;
        }
    }
}

@media(max-width:950px){
    .password-vault-page .benefits .left-right{
        padding:0 7.5%;
        .col1{
            width: 45%;
        }
        .other-elements{
            width: 47.5%;
        }
    }
}

@media(max-width:767px){
    .password-vault-page .benefits .left-right{
        padding:0 7.5%;
        margin-top: 0;
        .col1, .other-elements{
            width: 400px;
            max-width: 100%;
            margin:20px auto;
        }
    }
}