.password-vault-page .store .row1{
    margin:5% 0;
    padding:0 10%;
    h1{
        font-size: 35px;
        margin:0 25px;
    }
    .p{
        font-size: 18px;
        margin:15px 25px;
    }
    .items1{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .item1{
        width: 28.5%;
        padding:25px;
        border:1px solid transparent;
        border-radius: 10px;
        margin:25px 0;
        position: relative;
    }
    .icon{
        color:#427cff;
        font-size: 45px;
    }
    h6{
        font-size: 19px;
        margin:15px 0;
    }
    .item1 p{
        font-size: 18px;
        margin-bottom: 45px;
    }
    a{
        color:#427cff;
        display: flex;
        align-items: center;
        position: absolute;
        bottom:20px;
        font-size: 18px;
        svg{
            margin-left: 5px;
        }
    }
}

