    }
}

@media(max-width:767px){
    .password-vault-page .store .row1{
        margin:45px 0;
        padding:0 7.5%;
        .item1{
            width: 47.5%;
        }
    }
}

@media(max-width:520px){
    .password-vault-page .store .row1{
        h1{
