        width: 35%;
        margin-right: 2.5%;
    }
    .bg-img{
        content: url('../../../../assets/PasswordVault/bg-img.png');
        width: 100%;
        border-radius: 20px;
    }
    .top-img{
        content: url('../../../../assets/PasswordVault/top-img.png');
        position: absolute;
        width:100%;
        border-radius: 20px;
        right: -5%;
        top:10%;
