.password-vault-page .store .row3{
    margin:7.5% 0 5%;
    padding:0 7.5%;
    h1{
        font-size: 35px;
        margin-bottom: 35px;
        text-align: center;
    }
    .items2{
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
    }
    .item2{
        width: 29%;
        display: flex;
        align-items: center;
        border:1px solid #eee;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
        margin:0 2%;
        padding:25px 20px;
        border-radius: 7.5px;
        transition: 0.35s ease-in-out;
        &:hover{
            transform: scale(1.035);
            .icon-container{
                background: #d2dfff;
            }
        }
    }
    p{
        flex: 1;
        font-size: 18px;
    }
    .icon-container{
        width: fit-content;
        margin-right: 15px;
        padding:10px;
        background: #f5f8ff;
        border-radius: 69% 31% 43% 57% / 57% 60% 40% 43%;
    }
    .icon{
        width: 50px;
    }
}

